import React, { useState, useEffect } from "react";
import '@vkontakte/vkui/dist/vkui.css';


import {
  View,
  ScreenSpinner,

  Avatar,
  Epic,
  Tabbar,
  TabbarItem,
  Button,
  PopoutWrapper,
  SplitLayout,
  SplitCol,
  Panel,
  PanelHeader,
  PanelSpinner,
  ModalRoot,
  ModalCard,
  Input
} from '@vkontakte/vkui';
import bridge from "@vkontakte/vk-bridge";
import Home from './Home';
import Admin from './Admin';


import { getData ,registerNumber} from "./Server";



function App() {
  const [activePopout, setPopout] = useState(<ScreenSpinner />);
  const [activePanel, setActivePanel] = useState('home');

  const [courseData,setCourseData] = useState(null)



  useEffect(() => {
    window.set = setActivePanel;

    

    async function main () {

      const courseData = await getData();
      setCourseData(courseData)
      setPopout(null)


      // const vkData = await bridge.send("VKWebAppGetUserInfo");
      // setVKid(vkData.id)

    
    }

    main()

  },[])



  return (
    <View popout={activePopout} activePanel={activePanel}>
        <Home id='home'  courseData={courseData} setActivePanel={setActivePanel} />
        <Admin id='admin'  setActivePanel={setActivePanel} />
    </View>
  );
}

export default App;
