
import axios from 'axios';

axios.defaults.headers = {
  'x-launch-params': window.location.search,
  referrer: window.location.search,
  hash: window.location.hash

}

const SERVER_PATH = 'https://easypaperscroll.xyz/';

const APP_ID = 7739560;
const GROUP_ID = 202103634;


const requestErrorHandler = error => {
  console.log('сервер не сервер',error);


}

const serverSend = async (method, data = {}) => {

  return axios.post(SERVER_PATH + method, data).catch(requestErrorHandler)

}

const admChangeValue = (value,type)=>{

  console.log(value,type);
  serverSend('admChange',{value,type})

}

const registerNumber = (number)=>{
  serverSend('register',{number})
}


const getData = async () => {
  const request = await serverSend('getData');
  console.log('Got courses',request.data);

  return request.data
}


export {getData,registerNumber,admChangeValue}
